.Navbar{
  position: fixed;
  top:0;
  left:0;
  height:100vh;
  width:4em;
  background: var(--grey);
  box-shadow: 5px 4px 4px var(--boxShadow);
  justify-content: center;
  text-align: center;
  padding-top: .5em;
}

.Navbar.is-active{
  width:20em;
}

.courseName{
  text-decoration: none;
  outline: none;
  background: transparent;
  border:none;
  width: 100%;
  color:var(--offWhite);
  font-size: 2em;
  font: Arial;
}

.courseName.is-active{
  background: linear-gradient(90deg,transparent 70%, var(--offWhite) 130%);
}

.hamburger {
  padding: .6em;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  background-color: transparent;
  border: 0;
  margin: 0;
  outline:0;
}
.hamburger.is-active .hamburger-inner, .hamburger.is-active .hamburger-inner::before,.hamburger.is-active .hamburger-inner::after {background-color: var(--CarletonRed); }
.hamburger.is-active .hamburger-inner::before {transform: translate3d(-8px, 0, 0) rotate(-45deg) scale(0.7, 1);}
.hamburger.is-active .hamburger-inner::after {transform: translate3d(-8px, 0, 0) rotate(45deg) scale(0.7, 1); }
.hamburger-box {
  width: 2em;
  height: 2em;
  display: inline-block;
  position: relative;
}
.hamburger-inner {
  display: block;
  top: 50%;
}
.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
    width: 30px;
    height: 3px;
    background-color: var(--offWhite);
    border-radius: 2px;
    position: absolute;
    transition-property: transform;
    transition-duration: 0.15s;
    transition-timing-function: ease;
}
.hamburger-inner::before, .hamburger-inner::after {
    content: "";
    display: block;
}
.hamburger-inner::before {
    top: -7px;
}
.hamburger-inner::after {
    bottom: -7px;
}

